import { JSONContent } from "@tiptap/core";

import { Variable } from "../extensions/mention";
import { flatMapMentions } from "./mapMentions";

export function resolveMentions(
  contentResolved: JSONContent,
  variables: readonly Variable[]
) {
  if (contentResolved == null) {
    return null;
  }

  return flatMapMentions(contentResolved, (mention) => {
    const variableId = mention.attrs!.variable.id;
    const variable = variables.find((v) => v.id === variableId);

    return [
      {
        ...mention,
        attrs: {
          variable,
        },
      },
    ];
  });
}
