import { JSONContent } from "@tiptap/core";

export function flatMapMentions(
  contentUnresolved: JSONContent,
  cb: (node: JSONContent) => JSONContent[]
): JSONContent {
  if (contentUnresolved.type === "mention") {
    return cb(contentUnresolved);
  }

  if (contentUnresolved.content != null) {
    return {
      ...contentUnresolved,
      content: contentUnresolved.content.flatMap((c) => flatMapMentions(c, cb)),
    };
  }

  return contentUnresolved;
}
