import { Editor } from "@tiptap/core";
import { matchSorter } from "match-sorter";

import { Variable, MentionStorage } from "./mention.extension";

function match(variables: readonly Variable[], query: string) {
  return matchSorter(variables, query, {
    keys: [(option) => option.label],
    threshold: matchSorter.rankings.ACRONYM,
  });
}

export function getSuggestions(editor: Editor, query: string): Variable[] {
  const mentionStorage = editor.storage.mention as MentionStorage;
  const { variables } = mentionStorage;

  if (!variables) {
    return [];
  }

  return match(variables, query);
}
