import {
  createDocument,
  createMention,
  createParagraph,
  toTextNode,
} from "./plainToRichText";

export function curlyTemplate(template: string) {
  const content = template
    .split(/({{\w+}})/g)
    .filter((part) => part !== "")
    .map((part) =>
      /^{{\w+}}$/.test(part)
        ? createMention(part.slice(2, -2))
        : toTextNode(part)
    );

  return createDocument([createParagraph(content)]);
}
