import { Editor } from "@tiptap/core";
import { matchSorter } from "match-sorter";

import {
  CurlyBracketsVariable,
  CurlyBracketsStorage,
} from "./curly-brackets.extension";

function match(variables: readonly CurlyBracketsVariable[], query: string) {
  return matchSorter(variables, query, {
    keys: [(option) => option.label],
    threshold: matchSorter.rankings.ACRONYM,
  });
}

export function getCurlyBracketsSuggestions(
  editor: Editor,
  query: string
): CurlyBracketsVariable[] {
  const curlyBracketsStorage = editor.storage[
    "curly-brackets"
  ] as CurlyBracketsStorage;
  const { variables } = curlyBracketsStorage;

  if (!variables) {
    return [];
  }

  return match(variables, query);
}
