import { Editor, Extension } from "@tiptap/core";

export type ShiftEnterHandlerOptions = {
  handler: (props: { editor: Editor }) => boolean | undefined;
};

export const ShiftEnterHandler = Extension.create<ShiftEnterHandlerOptions>({
  name: "ShiftEnterHandler",
  addKeyboardShortcuts() {
    return {
      "Shift-Enter": (props) => {
        return this.options.handler(props) ?? false;
      },
    };
  },
});
