import {
  boolean,
  object,
  record,
  string,
  ZodOptional,
  ZodTypeAny,
  union,
  literal,
} from "zod";

import { zRichEditorContent } from "./zod-rich-editor-content";

export * from "./zod-rich-editor-content";

export { inputDefToZodObject } from "./input-def-to-zod-object";
export type { InputDef } from "./input-def-to-zod-object";

export const z = {
  literal,
  boolean,
  object,
  record,
  string,
  richEditorContent: zRichEditorContent,
  union,
};

export function unwrapOptional(type: ZodTypeAny): ZodTypeAny {
  if (type instanceof ZodOptional) {
    return unwrapOptional(type.unwrap());
  }
  return type;
}
