import { Node, mergeAttributes, NodeConfig } from "@tiptap/core";

export const CURLY_BRACKETS_SYMBOL = "{{";

export type CurlyBracketsVariable = {
  id: string;
  label: string;
  type: "string" | "number" | "boolean" | "email";
};

declare module "@tiptap/core" {
  interface Commands<ReturnType> {
    curlyBrackets: {
      insertCurlyBracketsSymbol: () => ReturnType;
    };
  }
}

export interface CurlyBracketsStorage {
  variables: readonly CurlyBracketsVariable[] | null;
  setVariables(newVariables: readonly CurlyBracketsVariable[] | null): void;
  menuContainer: HTMLDivElement | null;
}

export type CurlyBracketsOptions = {
  variables: readonly CurlyBracketsVariable[] | null;
};

export const curlyBracketsConfig: Partial<
  NodeConfig<CurlyBracketsOptions, CurlyBracketsStorage>
> = {
  name: "curly-brackets",
  // Increase the priority in order to load this before the EnterHandler extension.
  // We want to let the Curly Brackets extension handle the enter keypress first.
  priority: 110,
  group: "inline",
  inline: true,
  selectable: false,
  atom: true,

  addCommands() {
    return {
      insertCurlyBracketsSymbol: () => {
        return ({ chain }) =>
          chain().insertContent(CURLY_BRACKETS_SYMBOL).focus().run();
      },
    };
  },

  addStorage() {
    return {
      variables: this.options?.variables ?? null,
      setVariables(newVariables: CurlyBracketsVariable[]) {
        this.variables = newVariables;
      },
      menuContainer: null,
    };
  },

  addAttributes() {
    return {
      variable: {
        default: null,
        parseHTML: (element) => {
          return {
            id: element.getAttribute("data-variable-id"),
            label: element.getAttribute("data-variable-label"),
          };
        },
        renderHTML: (attributes) => {
          if (!attributes.variable) {
            return {};
          }

          return {
            "data-variable-id": attributes.variable.id,
            "data-variable-label": attributes.variable.label,
          };
        },
      },
    };
  },

  parseHTML() {
    return [
      {
        tag: `span[data-type="${this.name}"]`,
      },
    ];
  },

  renderHTML({ node, HTMLAttributes }) {
    return [
      "span",
      mergeAttributes({ "data-type": this.name }, HTMLAttributes),
      `${node.attrs.variable?.label}`,
    ];
  },

  renderText({ node }) {
    const variable = node.attrs.variable as CurlyBracketsVariable;
    return `${variable.label ?? variable.id}`;
  },

  addKeyboardShortcuts() {
    return {
      Backspace: () =>
        this.editor.commands.command(({ tr, state }) => {
          let isCurlyBrackets = false;
          const { selection } = state;
          const { empty, anchor } = selection;

          if (!empty) {
            return false;
          }

          state.doc.nodesBetween(anchor - 1, anchor, (node, pos) => {
            if (node.type.name === this.name) {
              isCurlyBrackets = true;
              tr.insertText(CURLY_BRACKETS_SYMBOL, pos, pos + node.nodeSize);

              return false;
            }
          });

          return isCurlyBrackets;
        }),
    };
  },
};

export const CurlyBrackets = Node.create<
  CurlyBracketsOptions,
  CurlyBracketsStorage
>(curlyBracketsConfig);
