import { unreachableCase } from "ts-assert-unreachable";
import { z } from "zod";

import { zRichEditorContent } from "./zod-rich-editor-content";

export type InputDef = {
  key: string;
  label: string;
  type: "string" | "plainText" | "dropdown";
};

export function inputDefToZodObject(inputs: InputDef[]) {
  return z.object(
    Object.fromEntries(
      inputs.map((i) => {
        if (i.type === "plainText") {
          return [i.key, z.string()];
        }
        if (i.type === "dropdown") {
          return [i.key, z.string()];
        }
        if (i.type === "string") {
          return [i.key, zRichEditorContent];
        }
        unreachableCase(i.type);
      })
    )
  );
}
