import type { RichEditorContent } from "../../rich-editor";
import type { JSONContent } from "@tiptap/core";

export function createDocument(content: JSONContent[]): RichEditorContent {
  return {
    type: "doc",
    content,
  };
}

export function createMention(variableId: string) {
  return {
    type: "mention",
    attrs: {
      variable: {
        id: variableId,
      },
    },
  };
}

export function createParagraph(content: JSONContent[]) {
  return {
    type: "paragraph",
    content,
  };
}

export function createDocumentWithMention(variableId: string) {
  return createDocument([createParagraph([createMention(variableId)])]);
}

export function plainToRichText(plainText: string): RichEditorContent {
  const content = plainText === "" ? undefined : [toTextNode(plainText)];

  return createDocument([
    {
      type: "paragraph",
      ...(content ? { content } : undefined),
    },
  ]);
}

export function toTextNode(plainText: string): JSONContent {
  return {
    type: "text",
    text: plainText,
  };
}
