import type { z, ZodType } from "zod";

export function zodGuard<T extends ZodType>(zodType: T) {
  return (x: unknown): x is z.infer<T> => passesValidation(zodType, x);
}

export function passesValidation<T extends ZodType>(
  zodType: T,
  x: unknown
): x is z.infer<T> {
  return zodType.safeParse(x).success;
}
