import { Editor, Extension } from "@tiptap/core";

export type EnterHandlerOptions = {
  handler: (props: { editor: Editor }) => boolean | undefined;
};

export const EnterHandler = Extension.create<EnterHandlerOptions>({
  name: "EnterHandler",
  addKeyboardShortcuts() {
    return {
      Enter: (props) => {
        return this.options.handler(props) ?? false;
      },
    };
  },
});
