import { Node, mergeAttributes, NodeConfig } from "@tiptap/core";

export const MENTION_SYMBOL = "@";

export type Variable = {
  id: string;
  label: string;
  type: "string" | "number" | "boolean" | "email";
};

declare module "@tiptap/core" {
  interface Commands<ReturnType> {
    mention: {
      insertMentionSymbol: () => ReturnType;
    };
  }
}

export interface MentionStorage {
  variables: readonly Variable[] | null;
  setVariables(newVariables: readonly Variable[] | null): void;
  menuContainer: HTMLDivElement | null;
}

export type MentionOptions = {
  variables: readonly Variable[] | null;
};

export const mentionConfig: Partial<
  NodeConfig<MentionOptions, MentionStorage>
> = {
  name: "mention",
  // Increase the priority in order to load this before the EnterHandler extension.
  // We want to let the Mention extension handle the enter keypress first.
  priority: 110,
  group: "inline",
  inline: true,
  selectable: false,
  atom: true,

  addCommands() {
    return {
      insertMentionSymbol: () => {
        return ({ chain }) =>
          chain().insertContent(MENTION_SYMBOL).focus().run();
      },
    };
  },

  addStorage() {
    return {
      variables: this.options?.variables ?? null,
      setVariables(newVariables: Variable[]) {
        this.variables = newVariables;
      },
      menuContainer: null,
    };
  },

  addAttributes() {
    return {
      variable: {
        default: null,
        parseHTML: (element) => {
          return {
            id: element.getAttribute("data-variable-id"),
            label: element.getAttribute("data-variable-label"),
            type: element.getAttribute("data-variable-type"),
          };
        },
        renderHTML: (attributes) => {
          if (!attributes.variable) {
            return {};
          }

          return {
            "data-variable-id": attributes.variable.id,
            "data-variable-label": attributes.variable.label,
            "data-variable-type": attributes.variable.type,
          };
        },
      },
    };
  },

  parseHTML() {
    return [
      {
        tag: `span[data-type="${this.name}"]`,
      },
    ];
  },

  renderHTML({ node, HTMLAttributes }) {
    return [
      "span",
      mergeAttributes({ "data-type": this.name }, HTMLAttributes),
      `${node.attrs.variable?.label}`,
    ];
  },

  renderText({ node }) {
    const variable = node.attrs.variable as Variable;
    return `${variable.label ?? variable.id}`;
  },

  addKeyboardShortcuts() {
    return {
      Backspace: () =>
        this.editor.commands.command(({ tr, state }) => {
          let isMention = false;
          const { selection } = state;
          const { empty, anchor } = selection;

          if (!empty) {
            return false;
          }

          state.doc.nodesBetween(anchor - 1, anchor, (node, pos) => {
            if (node.type.name === this.name) {
              isMention = true;
              tr.insertText(MENTION_SYMBOL, pos, pos + node.nodeSize);

              return false;
            }
          });

          return isMention;
        }),
    };
  },
};

export const Mention = Node.create<MentionOptions, MentionStorage>(
  mentionConfig
);
