import { JSONContent } from "@tiptap/core";
import { Transform } from "prosemirror-transform";

import { schema } from "../schema";
import { plainToRichText } from "./plainToRichText";

export function appendText(
  original: JSONContent,
  stringToAppend: string
): JSONContent {
  if (original == null) {
    return plainToRichText(stringToAppend);
  }

  const originalContent = schema.nodeFromJSON(original);
  const contentToAppend = schema.text(stringToAppend);
  const paragraph = originalContent.firstChild!;

  const tr = new Transform(paragraph).insert(
    paragraph.content.size,
    contentToAppend
  );
  return tr.doc.toJSON();
}
