import { z, ZodTypeAny } from "zod";

import { RichEditorContent, schema } from "../rich-editor";

export const zRichEditorContent = z
  .object({})
  .passthrough()
  .refine(
    (obj) => {
      try {
        schema.nodeFromJSON(obj);
        return true;
      } catch (err) {
        return false;
      }
    },
    {
      message: "Object is not valid RichEditorContent",
    }
  )
  .transform((obj) => obj as RichEditorContent);

const IS_Z_RICH_EDITOR_CONTENT = Symbol("IS_Z_RICH_EDITOR_CONTENT");
Object.assign(zRichEditorContent._def, { [IS_Z_RICH_EDITOR_CONTENT]: true });

export function isZRichEditorContent<T extends ZodTypeAny>(type: T) {
  return IS_Z_RICH_EDITOR_CONTENT in type._def;
}
