import { getText, JSONContent, TextSerializer } from "@tiptap/core";
import { Schema } from "prosemirror-model";

import { schema } from "../schema";

export function richTextToPlain(
  contentResolved: JSONContent,
  fallbackEmptyToLabel = false
): string {
  const blockSeparator = "\n\n";
  const contentNode = schema.nodeFromJSON(contentResolved);
  const textSerializers = getTextSeralizersFromSchema(schema);

  if (fallbackEmptyToLabel) {
    return getText(contentNode, {
      blockSeparator,
      textSerializers,
    });
  }

  return getText(contentNode, {
    blockSeparator,
    textSerializers: {
      ...textSerializers,
      mention: (props) => {
        if (props.node.attrs.value == null) {
          return "";
        }
        return textSerializers.mention(props);
      },
    },
  });
}

// https://github.com/ueberdosis/tiptap/blob/main/packages/core/src/helpers/getTextSeralizersFromSchema.ts
export function getTextSeralizersFromSchema(
  s: Schema
): Record<string, TextSerializer> {
  return Object.fromEntries(
    Object.entries(s.nodes)
      .filter(([, node]) => node.spec.toText)
      .map(([name, node]) => [name, node.spec.toText])
  );
}
